@charset "UTF-8";

/*basic styles*/

* {
  box-sizing: border-box;
  transition: all .5s;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

button {
  outline: none;
  border: 1px solid transparent;
  cursor: pointer;
}

.wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.section {
  height: 100%;
  width: 100%;
}

.container {
  width: 1180px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

/*base end*/

/************************************/

/*header*/

.header {
  padding: 25px 0;
  border-bottom: 1px solid #694858;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__col {
  display: flex;
  align-items: center;
}

.header__search {
  margin-left: 25px;
}

/*header end*/

/*intro section*/

.intro {
  background: url(../img/intro-bg.png) center center no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
}

.intro__wrapper {
  height: 100%;
  width: 100%;
  position: relative;
}

/**************************/

/*menu*/

.menu__list {
  display: flex;
}

.menu__item {
  margin-right: 50px;
  position: relative;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__item:after {
  content: '';
  display: inline-block;
  position: absolute;
  margin-top: 30px;
  height: 3px;
  width: 100%;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
}

.menu__item--active:after {
  opacity: 1;
  visibility: visible;
}

.menu__item:hover:after {
  opacity: 1;
  visibility: visible;
}

.menu__link {
  font-size: 13px;
  /* Приближение из-за подстановки шрифтов */
  font-weight: 400;
  text-transform: uppercase;
}

.menu__link:hover {
  color: grey;
}

/**********************************/

/*search input*/

.search {
  padding: 10px 15px;
  border: 1px solid transparent;
  outline: none;
  border-radius: 25px;
  color: #626161;
  font-size: 12px;
  /* Приближение из-за подстановки шрифтов */
  font-weight: 400;
}

/*************************/

/*logo*/

.logo__link {
  display: inline-block;
}

.logo__img {
  max-width: 100%;
  max-height: 100%;
}

/******************************/

/*button*/

.button {
  background-color: #8824cd;
  box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-size: 17.96px;
  /* Приближение из-за подстановки шрифтов */
  font-weight: 700;
  padding: 10px 30px;
  border-radius: 15px;
}

.button:hover {
  background-color: black;
}

.button--pink {
  background-color: #fff;
  color: #d54c63;
}

.button--pink:hover {
  color: #fff;
}

/******************************/

/*main block*/

.mainscreen {
  color: #fff;
  font-weight: 400;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mainscreen__content {
  display: inline-block;
  text-align: right;
  width: 50%;
}

.mainscreen__label {
  font-size: 49.98px;
  margin-bottom: 45px;
}

.mainscreen__button {
  margin-bottom: 15px;
}

.mainscreen__subtitle {
  font-size: 12px;
}

/****************************/

/*features section*/

.features {
  background-color: #2e3b4e;
  padding: 80px 0 100px 0;
  position: relative;
}

.features__wrapper {
  padding: 0 15%;
}

.features__list {
  display: flex;
  flex-wrap: wrap;
}

.features__item {
  width: 48%;
  margin-right: 20px;
  margin-bottom: 50px;
}

.features__item:nth-child(even) {
  margin-right: 0;
}

.features__item:nth-child(3) {
  margin-bottom: 0;
}

.features__item:last-child {
  margin-bottom: 0;
}

.features:after {
  content: '';
  height: 50px;
  width: 50px;
  background-color: #2e3b4e;
  position: absolute;
  top: 98%;
  left: 50%;
  transform: rotate(45deg) translateX(-50%);
}

/****************************/

/*description*/

.description {
  color: #fff;
  font-size: 15px;
}

.description__title {
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.description__content {
  font-weight: 400;
  line-height: 26px;
  font-size: 19px;
}

.description__content--fz15 {
  font-size: 15px;
}

/***************************/

/*pink section*/

.pink {
  background: url(../img/pink-bg.png) center center no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  padding: 150px 0;
}

.pink__content {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/***************************/

/*content block*/

.content {
  color: #3a4d60;
  font-weight: 400;
}

.content__title {
  margin-bottom: 15px;
  font-weight: 700;
}

.content__info {
  font-size: 19px;
  line-height: 30px;
  margin-bottom: 30px;
}

.content__info--fz15 {
  font-size: 15px;
  line-height: 26px;
  margin-bottom: 0;
}

.content__button {
  margin-top: 20px;
}

.content--other {
  color: #fff;
}

.content--other .content__title {
  font-size: 35px;
  font-weight: 300;
}

/**************************/

/*heading section*/

.heading__wrapper {
  display: flex;
  height: 100%;
  width: 100%;
}

.heading__content,
.heading__img {
  flex: 1;
}

.heading__content {
  padding-left: 50px;
  display: flex;
  align-items: center;
}

.heading__img {
  flex-shrink: 0;
}

.heading__img-pic {
  width: 100%;
  height: 100%;
}

.heading--reverse .heading__content {
  padding-left: 0;
  padding-right: 50px;
}

/******************************/

/*experience section*/

.experience {
  background: url(../img/experience-bg.png) center center no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
}

.experience__wrapper {
  padding: 150px 0;
  background-color: rgba(82, 26, 102, 0.8);
}

.experience__content {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/*******************************/

/*photo section*/

.photo {
  background: url(../img/photo-bg.png) center center no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  opacity: .8;
}

.photo__wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
}

.photo__info {
  display: inline-block;
  color: #fff;
  text-align: center;
}

.photo__label {
  display: inline-block;
  padding: 5px 10px;
  border: 2px solid #fff;
  border-radius: 20px;
  margin-bottom: 25px;
}

.photo__title {
  font-size: 40px;
  /* Приближение из-за подстановки шрифтов */
  font-weight: 400;
  margin-bottom: 20px;
}

.photo__block {
  display: inline-flex;
  align-items: center;
}

.photo__block-avatar {
  margin-right: 15px;
  height: 45px;
  width: 45px;
  border: 1px solid transparent;
  overflow: hidden;
  border-radius: 50%;
}

.photo__block-avatar_icon {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.photo__block-name {
  font-size: 13px;
  /* Приближение из-за подстановки шрифтов */
  font-weight: 600;
}

/*******************************/

/*fullscreen menu*/

.fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-color: black;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition-delay: .5s;
}

.fullscreen__header {
  position: relative;
}

.fullscreen__header .logo {
  position: absolute;
  top: 30px;
  left: 20px;
}

.fullscreen__menu {
  height: 100%;
  width: 100%;
}

.fullscreen__menu .menu {
  height: 100%;
  width: 100%;
  display: block;
}

.fullscreen__menu .menu__list {
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fullscreen__menu .menu__item {
  margin-right: 0;
  margin-bottom: 40px;
}

.fullscreen__menu .menu__item:last-child {
  margin-bottom: 0;
}

.fullscreen__menu .menu__item:after {
  display: none;
}

.fullscreen__menu .menu__link {
  font-size: 30px;
}

.is-active {
  opacity: 1;
  visibility: visible;
  transition-delay: .2s;
}

/*******************************/

/*сross block lines*/

.burger {
  display: none;
  height: 25px;
  width: 30px;
  position: fixed;
  top: 30px;
  right: 20px;
  z-index: 15;
  cursor: pointer;
}

.burger .line {
  background-color: #fff;
  display: inline-block;
  width: 30px;
  height: 4px;
  border: 1px solid black;
  border-radius: 2px;
  position: absolute;
  top: 0;
  left: 0;
}

.burger .line:nth-child(2) {
  top: 10px;
}

.burger .line:nth-child(3) {
  top: 20px;
}

.getCross .line {
  display: inline-block;
  transform-origin: 50% 50%;
  background-color: #fff;
}

.getCross .line:nth-child(1) {
  display: none;
}

.getCross .line:nth-child(2) {
  transform: rotate(45deg);
  top: 50%;
}

.getCross .line:nth-child(3) {
  transform: rotate(-45deg);
  top: 50%;
}

/******************************/

.overflow {
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .container {
    padding: 0 5%;
  }

  .header__col--right {
    flex: 1;
    justify-content: center;
  }

  .header__menu {
    display: none;
  }

  .header__search {
    margin-left: 0;
  }

  .menu {
    display: none;
  }

  .mainscreen {
    align-items: center;
  }

  .mainscreen__content {
    width: 100%;
    text-align: center;
  }

  .features__wrapper {
    padding: 0;
  }

  .features__item:nth-child(3) {
    margin-bottom: 25px;
  }

  .features__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }

  .description__title {
    text-align: center;
  }

  .heading {
    padding: 5% 0;
  }

  .heading__wrapper {
    flex-direction: column;
  }

  .heading__content {
    padding-left: 0;
  }

  .heading--reverse .heading__content {
    padding-right: 0;
  }

  .burger {
    display: block;
  }
}