/*basic styles*/
    *{
        box-sizing: border-box;
        transition:all .5s;
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        font-family: 'Open Sans', sans-serif;
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #fff;
    }

    button{
        outline: none;
        border: 1px solid transparent;
        cursor: pointer;
    }

    .wrapper{
        position: relative;
        height: 100%;
        width: 100%;
        overflow-x: hidden;
        scroll-behavior: smooth;
    }

    .section{
        height: 100%;
        width: 100%;
    }

    .container{
        width: 1180px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;

        @include tablets{
            padding: 0 5%;
        }
    }

/*base end*/

/************************************/

/*header*/
    
.header{
    padding: 25px 0;
    border-bottom: 1px solid #694858;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__col{
        display: flex;
        align-items: center;

        &--right{
            @include tablets{
                flex:1;
                justify-content: center;
            }
        }
    }

    &__menu{
        @include tablets{
            display: none;
        }
    }

    &__search{
        margin-left: 25px;

        @include tablets{
           margin-left: 0; 
        }
    }
}

/*header end*/

/*intro section*/

.intro{
    background: url(../img/intro-bg.png) center center no-repeat; 
    -webkit-background-size: cover;
    background-size: cover;

    &__wrapper{
        height: 100%;
        width: 100%;
        position: relative;
    }
}

/**************************/

/*menu*/

.menu{

    @include tablets{
        display: none;
    }

    &__list{
        display: flex;
    }

    &__item{
        margin-right: 50px;
        position: relative;

        &:last-child{
            margin-right:0;
        }

        &:after{
            content:'';
            display: inline-block;
            position: absolute;
            margin-top: 30px;
            height: 3px;
            width: 100%;
            background-color:#fff;

            opacity: 0;
            visibility: hidden;
        }

        &--active{
            &:after{
                opacity: 1;
                visibility: visible;
            }
        }

        &:hover{
            &:after{
                opacity: 1;
                visibility: visible;
            }
        }
    }

    &__link{
        font-size: 13px;/* Приближение из-за подстановки шрифтов */
        font-weight: 400;
        text-transform: uppercase;

        &:hover{
            color:grey;
        }
    }
}
/**********************************/

/*search input*/

.search{
    padding: 10px 15px;
    border: 1px solid transparent;
    outline: none;
    border-radius: 25px;

    color:  #626161;
    font-size: 12px;/* Приближение из-за подстановки шрифтов */
    font-weight: 400;
}

/*************************/

/*logo*/

.logo{

    &__link{
        display: inline-block;
    }

    &__img{
        max-width: 100%;
        max-height: 100%;
    }
}

/******************************/

/*button*/
.button{
    background-color:  #8824cd;
    box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.2);
    color:  #ffffff;
    font-size: 17.96px;/* Приближение из-за подстановки шрифтов */
    font-weight: 700;
    padding: 10px 30px;
    border-radius: 15px;

    &:hover{
        background-color:black;
    }

    &--pink{
        background-color: #fff;
        color: #d54c63;

        &:hover{
            color:#fff;
        }
    }
}

/******************************/

/*main block*/

.mainscreen{
    color: #fff;
    font-weight: 400;
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;

    @include tablets {
        align-items: center;
    }

    &__content{
        display: inline-block;
        text-align: right;
        width: 50%;

        @include tablets{
            width: 100%;
            text-align: center;
        }
    }

    &__label{
        font-size: 49.98px;
        margin-bottom: 45px;
    }

    &__button{
        margin-bottom: 15px;
    }

    &__subtitle{
        font-size: 12px;
    }
}

/****************************/

/*features section*/

.features{
    background-color: #2e3b4e;
    padding: 80px 0 100px 0;
    position: relative;

    &__wrapper{
        padding: 0 15%;

        @include tablets{
            padding: 0;
        }
    }

    &__list{
        display: flex;
        flex-wrap: wrap;
    }

    &__item{
        width: 48%;
        margin-right: 20px;
        margin-bottom: 50px;

        &:nth-child(even){
            margin-right:0;
        }

        &:nth-child(3){
            margin-bottom:0;

            @include tablets{
                margin-bottom: 25px;
            }
        }

        &:last-child{
            margin-bottom:0;
        }

        @include tablets{
            width: 100%;
            margin-right: 0;
            margin-bottom: 25px;
        }
    }

    &:after{
        content: '';
        height: 50px;
        width: 50px;
        background-color: #2e3b4e;

        position: absolute;
        top: 98%;
        left: 50%;
        transform: rotate(45deg) translateX(-50%);
    }
}
/****************************/

/*description*/

.description{
    color: #fff;
    font-size: 15px;

    &__title{
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 30px;

        @include tablets{
            text-align: center;
        }
    }

    &__content{
        font-weight: 400;
        line-height: 26px;
        font-size: 19px;

        &--fz15{
            font-size: 15px;
        }
    }
}

/***************************/

/*pink section*/

.pink{
    background: url(../img/pink-bg.png)center center no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    padding: 150px 0;

    &__content{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
}

/***************************/

/*content block*/

.content{
    color: #3a4d60;
    font-weight: 400;

    &__title{
        margin-bottom: 15px;
        font-weight: 700;
    }

    &__info{
        font-size: 19px;
        line-height: 30px;
        margin-bottom: 30px;

        &--fz15{
            font-size: 15px;
            line-height: 26px;
            margin-bottom: 0;
        }
    }

    &__button{
        margin-top: 20px;
    }
    

    &--other{
        color: #fff;

        .content__title{
            font-size: 35px;
            font-weight: 300;
        }
    }
}

/**************************/

/*heading section*/

.heading{

    @include tablets{
        padding:5% 0;
    }

    &__wrapper{
        display: flex;
        height: 100%;
        width: 100%;

        @include tablets{
            flex-direction: column;
        }
    }

    &__content, &__img{
        flex: 1;
    }

    &__content{
        padding-left: 50px;
        display: flex;
        align-items: center;

        @include tablets{
            padding-left: 0;
        }
    }

    &__img{
        flex-shrink: 0;

        &-pic{
            width: 100%;
            height: 100%;
        }
    }

    &--reverse{
        .heading__content{
            padding-left: 0;
            padding-right: 50px;

            @include tablets{
                padding-right: 0;
            }
        }
    }
}

/******************************/

/*experience section*/

.experience{
    background: url(../img/experience-bg.png)center center no-repeat;
    -webkit-background-size: cover;
    background-size: cover;

    &__wrapper{
        padding: 150px 0;
        background-color:  rgba(82, 26, 102, 0.8);    
    }

    &__content{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}


/*******************************/

/*photo section*/

.photo{
    background: url(../img/photo-bg.png) center center no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    opacity: .8;

    &__wrapper{
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 1;
    }

    &__info{
        display: inline-block;
        color: #fff;
        text-align: center;
    }

    &__label{
        display: inline-block;
        padding: 5px 10px;
        border:2px solid #fff;
        border-radius: 20px; 
        margin-bottom: 25px;
    }

    &__title{
        font-size: 40px;/* Приближение из-за подстановки шрифтов */
        font-weight: 400;
        margin-bottom: 20px;
    }

    &__block{
        display: inline-flex;
        align-items: center;

        &-avatar{
            margin-right: 15px;
            height: 45px;
            width: 45px;
            border: 1px solid transparent;
            overflow:hidden;
            border-radius: 50%;

            &_icon{
               height: 100%;
               width: 100%;
               object-fit: cover;
            }
        }

        &-name{
            font-size: 13px;/* Приближение из-за подстановки шрифтов */
            font-weight: 600;
        }
    }
}

/*******************************/


/*fullscreen menu*/

.fullscreen{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    background-color: black;
    width: 100%;
    height: 100%;

    opacity: 0;
    visibility: hidden;

    overflow: hidden;
    transition-delay: .5s;

    &__header{
        position: relative;

        .logo{
            position: absolute;
            top: 30px;
            left: 20px;
        }
    }

    &__menu{
        height: 100%;
        width: 100%;

        .menu{
            height: 100%;
            width: 100%;

            display: block;

            &__list{
                height: 100%;
                width: 100%;

                flex-direction: column;
                justify-content: center;
                align-items: center;
            }

            &__item{
                margin-right: 0;
                margin-bottom: 40px;
                
                &:last-child{
                    margin-bottom: 0;
                }

                &:after{
                    display:none;
                }
            }

            &__link{
                font-size: 30px;
            }
        }
    }
}

.is-active{
    opacity: 1;
    visibility: visible;
    transition-delay: .2s;
}

/*******************************/

/*сross block lines*/

.burger{
    display: none;
    height: 25px;
    width: 30px;

    position: fixed;
    top: 30px;
    right: 20px;

    z-index: 15;

    cursor: pointer;
    

    .line{
        background-color: #fff;
        display: inline-block;
        width: 30px;
        height: 4px;
        border: 1px solid black;
        border-radius: 2px;

        position: absolute;
        top: 0;
        left: 0;

        &:nth-child(1){

        }

        &:nth-child(2){
            top: 10px;
        }

        &:nth-child(3){
            top: 20px;
        }
    }

    @include tablets{
        display: block;
    }
}

// анимация в крестик
.getCross{

    .line{
        display: inline-block;
        transform-origin: 50% 50%;

        background-color: #fff;
        &:nth-child(1){
            display: none;
        }

        &:nth-child(2){
            transform: rotate(45deg);
            top: 50%;
        }

        &:nth-child(3){
            transform: rotate(-45deg);
            top: 50%;
        }
    }
}
/******************************/

.overflow{
    overflow:hidden;
}



